	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>Senior Registered Liberals across Canada identified the need for “stronger governmentefforts to create stable, quality jobs for all Canadians, with an emphasis on creating realopportunities for the younger generations” as a priority issue ;</p>\n</li>\n<li>\n<p>Statistics Canada estimates that the Canadian economy has lost 7% of its mid-skilledjobs during the past 10 years, resulting in a decline in median incomes during the pastdecade;</p>\n</li>\n<li>\n<p>Manymiddle-aged workers displaced by these losses have been forced to take lowerlevel part-time jobs with limited benefits. Similarly, many younger, well-educatedCanadians are also forced to take lower level part-time jobs that do not supportprogress toward a middle class life-style;</p>\n</li>\n<li>\n<p>While Canada has supported innovation through education, R&#x26;D, and tax incentives,there is limited evidence that such innovations translate into a sufficient number ofquality jobs for Canadians; and</p>\n</li>\n<li>\n<p>Other countries such as China, Germany and South Korea provide models of cooperation among government, industry and academia leading to globally competitivecorporations and associated jobs in new economic sectors;</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Government of Canada: </p>\n<ul>\n<li>\n<p>to provide leadership in partnership with Canadian businesses, unions and educationalinstitutions for identifying new business sectors in which Canadian corporations cancreate the necessary advantages to compete internationally thereby supporting stable,quality jobs for all Canadians; and</p>\n</li>\n<li>\n<p>to develop the necessary programs to ensure Canadian innovations translate into stable,quality jobs by encouraging and supporting Canadian corporations to grow and competein the global marketplace.(Seniors’Commission)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/the-need-for-stable%2C-quality-jobs--the-key-to-a-stronger-middle-class","date":"Mon Mar 05 2018 23:27:04 GMT-0500 (EST)","title":"The Need for Stable, Quality Jobs -The Key to a Stronger Middle Class"}}},"pathContext":{}}

